import React from "react";

import { graphql, Link, useStaticQuery } from "gatsby";
import { Helmet } from 'react-helmet';
import { GatsbyImage } from 'gatsby-plugin-image';

import Layout from "../components/layout_inner";

const Prices = (props) => {
  const data = useStaticQuery(graphql`
    query {
      portrait: file(relativePath: { eq: "categories/Индивидуальная/Анастасия/IMG_2379.jpg" }) {
        childImageSharp {
          gatsbyImageData(layout: CONSTRAINED, quality: 100, width: 768, placeholder: TRACED_SVG)
        }
      }
      family: file(relativePath: { eq: "categories/Семейная/Соня/IMG_9837.jpg" }) {
        childImageSharp {
          gatsbyImageData(layout: CONSTRAINED, quality: 100, width: 768, placeholder: TRACED_SVG)
        }
      }
      party: file(relativePath: { eq: "categories/Party/Девичник Крестины.jpg" }) {
        childImageSharp {
          gatsbyImageData(layout: CONSTRAINED, quality: 100, width: 768, placeholder: TRACED_SVG)
        }
      }
      wedding: file(relativePath: { eq: "categories/Свадьба/Татьяна + Александр/IMG_5423.jpg" }) {
        childImageSharp {
          gatsbyImageData(layout: CONSTRAINED, quality: 100, width: 768, placeholder: TRACED_SVG)
        }
      }
    }
  `);
  const categories = [
    {
      name: 'Индивидуальная фотосессия (портретная)',
      type: 'portrait'
    },
    {
      name: 'Семейная / Детская / Крестины / Love Story / В ожидании чуда',
      type: 'family'
    },
    {
      name: 'Девичник',
      type: 'party'
    },
    {
      name: 'Свадьба',
      type: 'wedding'
    }
  ];

  return (
    <Layout {...props}>
      <Helmet title="Фотограф Минск | Цены" defer={false} />
      <h1>
        Цены
      </h1>
      <p>&nbsp;</p>
      <ul className="pricing no-spacing">
        {categories.map(category => (
          <li key={category.type} className="row list-item accordion">
            <span>
              <GatsbyImage className="list-thumbnail" image={data[category.type].childImageSharp.gatsbyImageData} alt={category.name} />
            </span>
            <div>
              <h3>{category.name}</h3>
              <br />
              <div>
                {category.type === 'wedding' ? (
                  <>
                    <p>
                      На данный момент я предлагаю один вариант пакета: «Загс+прогулка»
                    </p>
                    <p>
                      Стоимость свадебной съемки – 140 бел. руб. / час.
                    </p>
                    <p>
                      Вы получаете не менее 120 фоторграфий с вашей свадьбы с цветокоррекцией, ретушью и кадрированием.<br />
                      Все фотографии разгружаются в облако. Скидываю Вам ссылку, по которой сможете скачать свои фотографии. Потери качества не происходит.<br />
                      Готовность 1 месяц.
                    </p>
                    <p>
                      Для бронирования даты мы встречаемся с вами и заключаем договор. Также вносится небольшая предоплата до 20% от стоимости услуги.<br />
                      При встрече с Вами обсуждаем все моменты и пожелания.<br />
                      Также поделюсь контактами хороших визажистов.
                    </p>
                    <p>
                      <Link to="/gallery/Свадьба/">Мои работы</Link>
                    </p>
                  </>
                ) : (
                  <>
                    <p>
                      Стоимость фотосессии – {category.type === 'portrait' ? 120 : 140} бел. руб. / час.
                    </p>
                    <p>
                      Локации могут быть разные: как на улице / в парке, так и в студии.<br />
                      Если желаете провести фотосессию в студии, то аренда зала оплачивается дополнительно.<br />
                      <Link to="/studios/">Список студий</Link>.
                    </p>
                    <p>
                      За 1 съемочный час вы получаете 40-70 фотографий в ретуши.<br />
                      Все фотографии загружаются в облако. Скидываю Вам ссылку, по которой сможете скачать свои фотографии. Потери качества не происходит.<br />
                      Готовность 2-3 недели.
                    </p>
                    <p>
                      Также можно воспользоваться услугами визажиста – оплата дополнительно.
                    </p>
                    <p>
                      ! Перед съемкой проведу консультацию. Помогу с выбором образа, подскажу что стоить взять, а что нет. Также поделюсь контактами хороших визажистов.
                    </p>
                    <p>
                      {category.type === 'portrait' ? <Link to="/gallery/Индивидуальная/">Мои работы</Link> : ''}
                      {category.type === 'family' ? (
                        <>
                          Мои работы
                          {' '}
                          <Link to="/gallery/Семейная/">Семейная</Link> / <Link to="/gallery/Love%20story/">Love Story</Link> / <Link to="/gallery/Беременность/">В ожидании чуда</Link>
                        </>
                      ) : ''}
                      {category.type === 'party' ? <Link to="/gallery/Party/">Мои работы</Link> : ''}
                    </p>
                  </>
                )}
              </div>
            </div>
          </li>
        ))}
      </ul>
    </Layout>
  );
};

export default Prices;
